.timeline-item {
	width: 50%;
	height: 100%;
	display: flex;
	flex-flow: row wrap;

	.break {
		flex-basis: 100%;
		width: 0px;
		height: 0px;
		overflow: hidden;
	}

	.block {
		justify-content: center;
		height: 50%;
		flex: 1 50%;
		display: inline-flex;
		align-items: center;
	}

	.block-img {
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}

	.block-year {
		position: relative;

		h3 {
			font-size: 120px;
			position: absolute;
			top: -18%;
			left: -33%;
			color: #33E9e2;
			font-style: italic;
			-webkit-text-stroke: 3px #33E9e2;
			-webkit-text-fill-color: transparent;
			margin: 0;
		}
	}

	.block-desc {

		p {
			padding: 16%;
		}
	}

}