.slogan {
	height: 45vh;
	margin: 0 auto;
	-webkit-backface-visibility: hidden;
	transform-style: preserve-3d;

	.content {
		font-style: italic;
		letter-spacing: -.3px;
		fill: #fff;
		font-size: .8rem;
		animation: ptfe_loop_around 30s infinite linear;
		transform-origin: 50% 50%;
		-webkit-backface-visibility: hidden;
		transform-style: preserve-3d;
		z-index: 2;
	}
	
	.shape {
		animation: ptfe_loop_self 25s infinite linear;
		transform-origin: 48% 53%;
		-webkit-backface-visibility: hidden;
		transform-style: preserve-3d;
		opacity: .8;
		z-index: 1;
	}
	
	.background {
		animation: ptfe_loop_self 15s infinite linear;
		transform-origin: 48% 51%;
		-webkit-backface-visibility: hidden;
		transform-style: preserve-3d;
		opacity: .8;
		z-index: 0;
	}
	
	.loop_it {
		transform: rotate(70deg);
		transform-origin: 50% 50%;
	}
	
	.frame{
		animation: ptfe_loop_self 20s infinite linear;
		transform-origin: 50% 50%;
		-webkit-backface-visibility: hidden;
		transform-style: preserve-3d;
	}
}


@keyframes ptfe_loop_self {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}

@keyframes ptfe_loop_around {
	0% {
		transform: translate3d(-4%, 0%, 0);
	}

	14.2% {
		transform: translate3d(-2%, 2%, 0);
	}

	28.4% {
		transform: translate3d(0%, 4%, 0);
	}

	42.6% {
		transform: translate3d(2%, 2%, 0);
	}

	56.8% {
		transform: translate3d(4%, 0%, 0);
	}

	71% {
		transform: translate3d(0%, -4%, 0);
	}

	85% {
		transform: translate3d(-2%, -2%, 0);
	}

	100% {
		transform: translate3d(-4%, 0%, 0);
	}
}