.timeline {
	position: relative;

	.timeline-wrapper {
		padding: 5% 0 0 0;
		// overflow-y: hidden;
		width: 100vw;
		height: 60vh;
		display: -webkit-box;
	}
}

.timeline-slider {
	position: absolute;
	width: 100%;
	height: 100%;


	.timeline-slider-gradient {
		margin-top: 5%;
		padding-bottom: 5%;
		height: 80%;
		background-image: linear-gradient(54deg, #33E9e2, transparent 70%);
	}

	.timeline-slider-rail {
		height: 10%;
		position: relative;
		margin: 0 auto;
		max-width: 870px;
		height: 10%;
		position: relative;
		margin: 2rem auto 0 auto;
		z-index: 3;

		input {
			width: 100%;
		}
	}

	.timeline-slider-button {
		cursor: move;
		position: absolute;
	}

	.timeline-slider-dates {
		display: flex;
		justify-content: space-between;
		width: 104%;
		text-align: center;
		left: -2%;
		position: absolute;
		top: 25%;
		font-size: .9em;
	}
}