$gap: 80%;
$itemHeight: 20rem;
$sliderWidth: 900px;

.imageslider {
	position: relative;
	height: $itemHeight + ($itemHeight / 3);
	width: $sliderWidth;
	margin: 0 auto;

	.imageslider-wrapper {
		position: absolute;
		display: flex;
		width: 1600px;
		left: 50%;
		transform: translateX(-50%);

		.imageslider-item {
			height: $itemHeight;
			width: $itemHeight;
			border: 1px solid cadetblue;
			background-position: center;
			background-size: cover;
			background-repeat: no-repeat;
			border-radius: 4px;
			position: relative;

			&:after {
				content: '';
				width: 100%;
				height: 2rem;
				position: absolute;
				background-image: radial-gradient(#cbcbcb , transparent 40%);
				bottom: -15%;
			}
			&:first-child {
				opacity: 0;
				transform: scale(.4) translateX($gap * 4);
			}
			&:nth-child(2) {
				opacity: .5;
				transform: scale(.7) translateX($gap);
			}
			&:nth-child(3) {
				z-index: 1;
			}
			&:nth-child(4) {
				opacity: .5;
				transform: scale(.7) translateX(-$gap);
			}
			&:last-child {
				opacity: 0;
				transform: scale(.4) translateX(-$gap * 4);
				z-index: -1;
			}
		}
	}
	
	.imageslider-nav {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
	}
}
